import { departmentManagerApi } from "./departmentManagerApi";
import {
  departmentAllHolidayFunction,
  departmentCreatedHolidayFunction,
  departmentDeleteHolidayFunction,
  departmentTabControllerFunction,
  departmentTabManageFunction,
  instituteAddUniversalBatchFunction,
  instituteAllUniversalBatchFunction,
  instituteSelectUniversalBatchFunction,
  instituteLinkUniversalBatchFunction,
} from "./department_manager_query_mutation_function";

const departmentManagerApiDynamic = departmentManagerApi.injectEndpoints({
  endpoints: (builder) => ({
    departmentTabManage: departmentTabManageFunction(builder),
    departmentTabController: departmentTabControllerFunction(builder),
    departmentCreatedHoliday: departmentCreatedHolidayFunction(builder),
    departmentAllHoliday: departmentAllHolidayFunction(builder),
    departmentDeleteHoliday: departmentDeleteHolidayFunction(builder),
    instituteAddUniversalBatch: instituteAddUniversalBatchFunction(builder),
    instituteAllUniversalBatch: instituteAllUniversalBatchFunction(builder),
    instituteSelectUniversalBatch:
      instituteSelectUniversalBatchFunction(builder),
    instituteLinkUniversalBatch: instituteLinkUniversalBatchFunction(builder),

    // instituteSelectUniversalBatchFunction:instituteSelectUniversalBatchFunction(builder),
    // instituteSelectUniversalBatchFunction:instituteSelectUniversalBatchFunction(builder),
  }),
});
export const {
  useDepartmentTabManageQuery,
  useDepartmentTabControllerMutation,
  useDepartmentCreatedHolidayMutation,
  useDepartmentAllHolidayQuery,
  useDepartmentDeleteHolidayMutation,

  useInstituteAddUniversalBatchMutation,
  useInstituteAllUniversalBatchQuery,
  useInstituteSelectUniversalBatchMutation,
  useInstituteLinkUniversalBatchMutation,
} = departmentManagerApiDynamic;
