import { DELETE, PATCH, POST } from "../UrlConfig/MethodType";

export const departmentTabManageFunction = (builder) => {
  return builder.query({
    query: (data) =>
      `/v1/department/tab/manage/${data?.did}/query?flow=${data?.flow ?? ""}`,
    providesTags: ["Tab_Manage"],
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

export const departmentTabControllerFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v1/department/tab/manage/${data?.did}/query`,
      method: PATCH,
      body: data.tabManage,
    }),
    invalidatesTags: ["Tab_Manage"],
  });
};

export const departmentCreatedHolidayFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v1/attendance/department/${data.did}/holiday`,
      method: POST,
      body: data.holiday,
    }),
  });
};

export const departmentAllHolidayFunction = (builder) => {
  return builder.query({
    query: (data) =>
      `/v1/attendance/holiday/${data?.did}?page=${data?.page}&limit=${data?.limit}`,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response, meta, arg) => {
      if (arg?.search)
        return {
          search: true,
          list: response?.depart?.holiday,
        };
      else
        return {
          page: arg?.page,
          list: response?.depart?.holiday,
        };
    },
    merge: (currentCache, incomingData) => {
      if (incomingData?.search) return incomingData;
      else if (incomingData?.page === 1) return incomingData;
      else return void currentCache?.list?.push(...incomingData?.list);
    },
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

export const departmentDeleteHolidayFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v1/attendance/holiday/${data.hid}/delete`,
      method: DELETE,
    }),
  });
};

